@use '../helpers' as *;

@mixin nav {
    .nav {
        transition: right 0.75s ease-out;

        .nav-link {
            color: $netflix-white;

            &.active {
                font-weight: 500;
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .info-btn {
        // position: absolute;
        // top: 2px;
        // right: 80px;
    }

    @include lg-down {
        .nav {
            position: fixed;
            bottom: 0;

            z-index: 4;
            width: 50vw;
            right: -100vw;

            height: calc(100vh - 128px);
            background: $netflix-black;
            box-shadow: 0 0 4px 4px rgba(14, 14, 14, 0.5);
            border-radius: 4px;
            align-items: center;
            .nav-link {
                transition: filter 0.25s ease;
                @include font-size(18px, 500);
                text-align: center;
                margin-bottom: 24px;
                text-shadow: 0 -1px 4px #fefcf8, 0 -2px 10px #e5e568, 0 -10px 20px #e8843f, 0 -18px 40px #e50914;
                &:hover {
                    filter: drop-shadow(16px 16px 20px #f40612) contrast(200%);
                    text-decoration: none;
                }
            }
        }

        .burger-btn {
            z-index: 5;
            // right: 0;
            // transition: position 0.25s ease-out 0.75s;
            // position: fixed;
            // right: 12px;
            // top: 0;
        }

        #burger-btn:checked {
            & ~ .burger-btn {
                // position: fixed;
                // right: calc(50vw - 2 * v-get('burger-size'));
                // transition: right 0.75s ease-out;
            }
            & ~ .nav {
                right: 0;
                .nav-link {
                }
            }
        }
    }
}
